body{
  color: white;
  background-color: black;
  text-align: center;  
  padding: 20px;
}
.dual-container{
  display: flex; 
  justify-content: center;
}
#break-label{
  display: flex; 
  justify-content: center;
}

#break-handler,#session-handler{
  display: flex; 
  width: 320px;
  align-items: center;
  justify-content: center;
}
#break-handler button,#session-handler button
{
  height: 40px; 
  width: 40px; 
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 20px;
  
}

#start_stop, #reset{
margin: 20px;
}

